.main {
    min-height: 600px;
    display: flex;
    flex-wrap: wrap;
    .wrapper {
        max-width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        .text {

            font-family: 'Montserrat', sans-serif;
            font-size: 35px;
            font-weight: 600;
            color: #1e2125;;
            @media screen and (max-width: 600px) {
                margin-top: 30px;
                font-size: 20px;

            }
        }
        .contacts {
            display: flex;
            flex-direction: column;
            .contactsText {
                font-family: 'Montserrat', sans-serif;
                font-size: 15px;
                font-weight: 600;
                color: #3c3e41;
            }
            .socialItems {
                margin-top: 20px;
                display: flex;
                .item{
                    display: block;
                    margin-right: 20px;
                    border-radius: 10px;

                    width: 60px;
                    height: 60px;
                    background: linear-gradient(145deg,#e2e8ec,#fff);
                    box-shadow: 5px 5px 15px #d1d9e6,-5px -5px 15px #fff;
                }
                .item:hover img {
                    padding: 5px;
                    transition: all 0.3s;
                }
                .item img{
                    padding: 10px;
                    width: 60px;
                    height: 60px;
                }
            }
        }
    }
    .photo {
        margin-top: 50px;
        width: 400px;
        height: 500px;
        position: relative;
        background: linear-gradient(145deg,#e2e8ec,#fff);
        box-shadow: 5px 5px 15px #d1d9e6,-5px -5px 15px #fff;
        .image {
            background: url("../image/IMG_638.jpg") center center/cover no-repeat;
            width: 97%;
            height: 97%;
            margin: 0 auto;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);


        }
    }
}












